:root {
  --brandColor: #3a4793;
  --secondaryColor: #2583ec;
  --altBrandColor: #9787ee;
  --altSecondaryColor: #ffbf3e;
  --textColor: #ffffff;
}

html,
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Flex Easy Grid */

/* Grid */

.column {
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
}

@media screen and (min-width: 800px) {
  .row {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
  }
  .column {
    -webkit-flex: 1 1;
            flex: 1 1;
  }
  ._25 {
    -webkit-flex: 2.5 1;
            flex: 2.5 1;
  }
  ._5 {
    -webkit-flex: 5 1;
            flex: 5 1;
  }
}
/* Style */

.column {
  background: #343436;
}

/* critical thinking skills section */
.criticalThinkingContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background: var(--secondaryColor);
  padding: 20px 0;
  height: 40vh;
}

.criticalThinkingHeader {
  font-size: 40px;
  color: var(--textColor);
  border-bottom: 5px solid var(--altSecondaryColor);
}

.home {
  width: 100%;
  height: 50vh;
  background: url(/critical-thinking/static/media/mountains.a9506dd8.png) center center;
  background-repeat: no-repeat;
  background-size: cover;
  color: var(--textColor);
}

/* add a blue transparent background to the background image */
.homeContainer {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background: rgba(58, 71, 147, 0.4);
}
/* add padding to the header text */
.homeHeader {
  padding: 10px 20px;
  font-size: 80px;
  text-align: center;
  font-weight: 800;
}

.articleContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 40vh;
}

.headerContainer {
  -webkit-flex: 1 1;
          flex: 1 1;
  height: 100%;
  background: var(--brandColor);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.header {
  -webkit-flex: 1 1;
          flex: 1 1;
  text-align: center;
  font-size: 50px;
  color: var(--textColor);
}

@media (max-width: 800px) {
  .articleContainer {
    display: block;
    height: 100%;
  }
}

.articleList {
  -webkit-flex: 2 1;
          flex: 2 1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background: var(--altBrandColor);
  list-style: none;
  padding: 0;
  margin: 0;
  height: 100%;
}

.articleList li {
  font-size: 20px;
  color: var(--textColor);
  font-weight: 600;
}

@media (max-width: 800px) {
  .articleList {
    padding: 20px;
  }
}

/* start footer section */
.footerContainer {
  display: -webkit-flex;
  display: flex;
  background: var(--brandColor);
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 20px;
}

.footerHeader {
  font-size: 50px;
  color: var(--textColor);
}

.footerParagraph {
  font-size: 20px;
  color: var(--textColor);
}

.footerLink {
  text-decoration: none;
  font-size: 20px;
  color: var(--textColor);
}

